.classicism-romanticism {
    height: 210px;
    background-image: url('../images/img/classicism-romanticism-bg-img.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: end;

    @include on-tablet {
      height: 390px;
    }

    @include on-desktop {
      height: 550px;
    }

  &__content {
    padding: 20px 0;
    background: #FFF;
    box-shadow: 0 2px 0 0 rgba(26, 90, 76, 0.25);
    transform: translateY(55%);

    @include on-tablet {
      padding: 45px 100px 50px 55px;
    }

    @include on-desktop {
      padding: 45px 100px 50px;
    }
  }

  &__data {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 20px;

    @include on-tablet {
      justify-content: space-between;
      margin-bottom: 16px;
    }
  }

  &__event {
    color: $color-text-third;
    font-size: 16px;
    font-weight: 500;
    line-height: 100%;

    @include on-tablet {
      font-size: 14px;
    }
  }

  &__date {
    color: $color-border;
    font-weight: 700;
    line-height: 150%;

    @include on-tablet {
      font-size: 14px;
      margin-right: 50px;
    }
  }

  &__title {
    color: $color-text-second;
    font-family: Montserrat, sans-serif;
    text-align: center;
    font-size: 28px;
    font-weight: 400;
    line-height: 100%;

    @include on-tablet {
      font-size: 36px;
      text-align: left;
    }
  }
}

